import React, { useRef } from 'react';
import Slick from 'react-slick';
import styled from 'styled-components';

const Carousel = ({ className='', children, slides=1, onChange }) => {
  const carouselRef = useRef(null);
  const slickSettings = {
    arrows: false,
    infinite: false,
    dots: false,
    variableWidth: true,
    dot: false,
    appendDots: dots => (
      <div className="nav-container">
        <button data-direction="prev" onClick={handleClickArrowButton}>123</button>
        <ul>{dots}</ul>
        <button data-direction="next" onClick={handleClickArrowButton}>345</button>
      </div>),
    afterChange: (index) => onChange && onChange(index)
  };

  function handleClickArrowButton(e) {
    if (carouselRef.current) {
      const { direction } = e.currentTarget.dataset;
      if (direction === 'prev') carouselRef.current.slickPrev();
      if (direction === 'next') carouselRef.current.slickNext();
    }
  }


  return (
    <Root >
      <CarouselBody>
        <Slick className={className} ref={carouselRef} {...slickSettings}>
          { children.map((child, i) => <div key={i}>{ child }</div>) }
        </Slick>
      </CarouselBody>
    </Root>
  )
}

const Root = styled.div`
  position: relative;
  padding-bottom: 24px;
  *:focus {
    outline: none;
  } 
  .slick-dots {
    display: flex;
    justify-content: center;
    button {
        border: 0;
        margin: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        background: transparent;
        svg {
          width: 6px;
        }
    }
    ul {
      li, button {
        border: 0;
        border-radius: 2px;
        margin: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        font-size: 14px;
        font-weight: 500;
        line-height: 1;
      }
      button:before {
        content: none;
      }
      .slick-active button {}
    }
  }
`

const CarouselBody = styled.div`
  position: relative;
  width: 100%;
  box-sizing: border-box;
  .slick-list {
    overflow: visible;
  }
`

export default Carousel;
