import React, { useEffect, useRef } from 'react';
import styled, { css } from 'styled-components';
import { colors } from '../../constants/colors';
import { lockWindow } from '../../utils/methods';
import { respondTo } from '../../utils/responsive';

const ModalBox = ({ open, onClose, children, ...props }) => {
  const mouseStateRef = useRef({
    click: false,
    move: false
  });

  useEffect(() => {
    lockWindow(open);
  }, [open]);

  function handleMouseDown() {
    mouseStateRef.current.click = true;
    mouseStateRef.current.move = false;
  }
  function handleMouseMove() {
    if (mouseStateRef.current.click) {
      mouseStateRef.current.move = true;
    }
  }
  function handleMouseUp() {
    mouseStateRef.current.click = false;
  }

  function handleClickBox() {
    if (!mouseStateRef.current.move) {
      onClose();
    }
  }

  return (
    <Root open={open} {...props}>
      <Mask onClick={onClose} />
      <Box open={open} 
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onClick={handleClickBox}
      >{ children }</Box>
    </Root>
  )
}

const Root = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  z-index: 10;
  transition: all .3s ease;
  visibility: hidden;
  pointer-events: none;
  ${({ open }) => open && css`
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  `};
`

const Mask = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: ${colors.black};
  opacity: 0.75;
  z-index: 0;
`

const Box = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  ${respondTo.md} {
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
  }
`

const CloseButton = styled.button`
  position: absolute;
  right: 12px;
  top: 12px;
  width: 60px;
  height: 60px;
  border: 0;
  background: transparent;
  display: none;
  z-index: 2;
  ${respondTo.md} {
    display: block;
  }
  img {
    width: 100%;
    height: auto;
    transform: rotate(45deg);
  }
`

export default ModalBox;
